@import url("https://fonts.googleapis.com/css2?family=Nabla&family=Syne:wght@700;800&family=Urbanist:wght@100;400;900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Urbanist", sans-serif;
  scroll-behavior: smooth;
  font-size: 10px;
}

body {
  overflow-x: hidden;
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 9999999;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loader > img {
  width: 300px;
}
.loader.hidden {
  animation: fadeOut 0.2s;
  animation-fill-mode: forwards;
}

@keyframes fadeOut {
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
.contact-section {
  z-index: 10;
  position: fixed;
  background-color: black;
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  width: 50px;
  height: 100vh;
}
.contact-section:hover {
  cursor: pointer;
}
.contact-section .contact-div {
  padding: 10px;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  text-orientation: sideways;
  font-weight: 400;
  letter-spacing: 2px;
  height: 100vh;
}
.contact-section .contact-div .loop-container {
  white-space: nowrap;
  height: 100vh;
  display: flex;
  justify-content: space-between;
}
.contact-section .contact-div .loop-container .item {
  font-size: 2rem;
  position: absolute;
  bottom: 0;
  left: -15px;
  color: white;
}
.contact-section .contact-div .loop-container .item .contact-bold {
  font-weight: 900;
  font-size: 2rem;
}
.contact-section .contact-div .loop-container .item .contact-dot {
  font-size: 2rem;
  padding: 15px;
  font-weight: 900;
}

.logo {
  position: absolute;
  left: 200;
  top: 40;
  transition: transform 0.2s ease;
}
.logo:hover {
  transform: scale(0.7) rotate(45deg);
}

nav {
  position: fixed;
  right: 10%;
  z-index: 10;
}
nav ul {
  list-style: none;
}
nav ul li a {
  font-size: 5rem;
  text-decoration: none;
  color: white;
  font-weight: 100;
}

.hero {
  width: 100vw;
  background-color: black;
}
.hero .bg-heading {
  position: absolute;
  font-family: "Nabla", cursive;
  width: 100%;
  height: 100%;
  font-size: 450px;
  opacity: 0.05;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero .bg-heading::selection {
  background: rgb(255, 208, 0);
}
.hero-content-main {
  height: 100vh;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 200px;
}
.hero-content-main h4 {
  font-size: 2rem;
}
.hero-content-main > * {
  font-family: "Syne", sans-serif;
}
.hero-content-main .hero-heading-div {
  position: relative;
  height: 115px;
}
.hero-content-main .hero-heading-div .hero-heading-1,
.hero-content-main .hero-heading-div .hero-heading-2,
.hero-content-main .hero-heading-div .hero-heading-3 {
  position: absolute;
  font-family: "Syne", sans-serif;
  overflow: hidden;
  display: inline-block;
  font-weight: 800;
  -webkit-text-stroke: 2px;
  -webkit-text-fill-color: transparent;
  text-shadow: -6px -3px 0 rgb(255, 255, 255);
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
}
.hero-content-main .hero-heading-div .hero-heading-1 .char,
.hero-content-main .hero-heading-div .hero-heading-2 .char,
.hero-content-main .hero-heading-div .hero-heading-3 .char {
  font-size: 10rem;
  font-family: "Syne", sans-serif;
  transform: translateY(200px);
  transition: transform 0.5s;
}
.hero-content-main h2,
.hero-content-main h4 {
  font-weight: 800;
}
.hero-content-main h2 {
  font-size: 5rem;
}
.hero-content-sub {
  position: absolute;
  top: 80%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.hero-desc {
  color: white;
  margin-left: 200px;
  width: 400px;
  font-weight: 400;
  letter-spacing: 1px;
}
.hero-desc p {
  font-size: 1.8rem;
}
.hero-mail-btn {
  margin-right: 200px;
}
.hero-mail-btn button {
  background-color: transparent;
  padding: 2rem 30rem 2rem 2rem;
  color: white;
  text-align: left;
  font-size: 18px;
  font-family: "Syne", sans-serif;
  border: 2px solid white;
  position: relative;
}
.hero-mail-btn button:hover {
  background-color: white;
  cursor: pointer;
}
.hero-mail-btn button:hover::after {
  content: attr(data-value);
  color: black;
  width: 100%;
  position: absolute;
  left: 20;
}

.work {
  background-color: black;
}
.work-title {
  margin-left: 200px;
}
.work-title h4 {
  color: white;
  font-size: 1.6rem;
  padding: 40px 0px;
  font-family: "Syne", sans-serif;
  font-weight: 800;
}
.work-title hr {
  position: relative;
  left: -5%;
}
.work-headings {
  position: relative;
  padding: 20px 0;
  margin-left: 200px;
}
.work-headings span {
  position: absolute;
  font-family: "Urbanist", sans-serif;
  font-size: 12px;
  font-weight: 400;
  text-shadow: none;
  color: white;
  -webkit-text-stroke: 0px white;
  -webkit-text-fill-color: white;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 2px 5px;
  border-radius: 5px;
}
.work-headings .work-label-kk, .work-headings .work-label-pc, .work-headings .work-label-dh, .work-headings .work-label-cs {
  opacity: 0;
}
.work-headings h1 {
  display: inline-block;
  padding: 20px 0;
  font-family: "Syne", sans-serif;
  font-size: 10rem;
  font-weight: 800;
  border-bottom: 1px solid white;
  -webkit-text-stroke: 1px white;
  -webkit-text-fill-color: transparent;
  text-shadow: -6px -3px 0 rgb(255, 208, 0);
}
.work-designs {
  background-color: white;
  width: 100%;
  padding: 50px 0px;
}
.work-designs-1, .work-designs-2 {
  padding: 10px 0;
  display: flex;
  justify-content: space-evenly;
}

.contacts-divider-line {
  opacity: 0;
  height: 2px;
  width: 80vh;
  transform: scale(0);
  background-color: white;
  position: fixed;
}

.contact {
  clip-path: circle(0%);
  height: 100vh;
  width: 100vw;
  background-color: black;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.contact-white-bg {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: white;
  clip-path: circle(0%);
}
.contact .close-btn {
  background-color: white;
  color: black;
  border: none;
  border-radius: 50%;
  padding: 40px 32px;
  font-size: 18px;
  font-weight: 900;
  position: absolute;
  top: 5%;
  right: 10%;
}
.contact .close-btn:hover {
  cursor: pointer;
}
.contact-socials {
  transform: translateY(-40%);
}
.contact-socials h1 {
  margin-bottom: -30px;
  transform: translateY(-30px);
}
.contact-socials h1 a {
  font-size: 8rem;
  font-family: "Syne", sans-serif;
  font-weight: 800;
  text-decoration: none;
  display: inline-block;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 1px white;
  text-shadow: 0px 0px 0 rgb(255, 255, 255);
  transition: text-shadow 0.2s ease;
}
.contact-socials h1 a:hover {
  text-shadow: -6px -3px 0 rgb(255, 255, 255);
  -webkit-text-stroke: 1px white;
}
.contact-socials #insta a {
  letter-spacing: 8px;
}
.contact-socials #wapp a {
  letter-spacing: 5px;
}
.contact-socials #drib a {
  letter-spacing: 6px;
}
.contact-socials #link a {
  letter-spacing: 2px;
}
.contact-socials #twitt a {
  letter-spacing: -5px;
}
.contact-direct {
  transform: translate(-60%, 40%);
}
.contact-direct button {
  position: relative;
  opacity: 0;
  display: block;
  margin: 40px 0;
  font-size: 18px;
  color: white;
  padding: 35px 40px;
  width: 300px;
  border-radius: 50px;
  background-color: transparent;
  border: 1px solid white;
  transition-property: background-color, color;
}
.contact-direct button:hover {
  background-color: white;
  color: black;
  cursor: pointer;
}
.contact-direct button:hover::after {
  content: attr(data-value);
  color: black;
  background-color: white;
  width: 100%;
  position: absolute;
  left: 0;
}

.form {
  display: flex;
  background-color: black;
  padding: 100px 0;
  color: white;
}
.form label {
  font-size: 1.8rem;
}
.form-heading {
  margin-left: 200px;
}
.form-heading h1 {
  font-family: "Syne", sans-serif;
  font-weight: 800;
  font-size: 100px;
}
.form-heading h4 {
  font-family: "Syne", sans-serif;
  font-weight: 800;
  font-size: 40px;
}
.form-elements {
  display: flex;
  flex-direction: column;
  padding: 40px 0;
  margin-left: 200px;
}
.form-elements label {
  font-weight: 900;
  margin-top: 50px;
}
.form-elements input {
  font-size: 18px;
  letter-spacing: 1px;
  padding: 10px 0;
  width: 500px;
  background-color: transparent;
  border-top-style: hidden;
  border-left-style: hidden;
  border-right-style: hidden;
  color: white;
  border-bottom: 1px solid white;
}
.form-elements input:focus {
  outline: none;
}
.form-elements textarea {
  padding: 10px 0;
  font-size: 18px;
  letter-spacing: 1px;
  color: white;
  border-top-style: hidden;
  border-left-style: hidden;
  border-right-style: hidden;
  border-bottom: 1px solid white;
  width: 500px;
  background-color: transparent;
  resize: none;
}
.form-elements textarea:focus {
  outline: none;
}
.form-shakehands {
  overflow: hidden;
  background-image: url("./../res/shakehands.webp");
  background-repeat: no-repeat;
  width: 400px;
  height: 300px;
  position: relative;
  right: 60%;
  top: 30%;
}

.footer {
  font-size: 1.6rem;
  text-align: center;
  padding: 20px 0;
  background-color: black;
  color: white;
  font-family: "Syne", sans-serif;
}
.footer span {
  color: red;
}

.close-btn {
  text-decoration: none;
  background-color: white;
  color: black;
  border: none;
  border-radius: 50%;
  padding: 40px 32px;
  font-size: 18px;
  font-weight: 900;
  position: absolute;
  top: 5%;
  right: 10%;
  transition: 0.2s ease;
  transition-property: filter, scale;
}
.close-btn:hover {
  scale: 0.9;
  filter: drop-shadow(0px 15px 50px rgba(0, 0, 0, 0.1));
  cursor: pointer;
}

.header {
  color: white;
  padding: 18rem 20rem 5rem 20rem;
}
.header-aboutus {
  background-color: black;
}
.header h1 {
  font-family: "Syne";
  font-weight: 900;
  font-size: 8rem;
}
.header .line {
  height: 1px;
  width: 40vw;
  background-color: rgba(255, 255, 255, 0.7);
}
.header p {
  width: 500px;
  margin-top: 80px;
  font-size: 20px;
  line-height: 25px;
  letter-spacing: 1px;
}
.header .profile-pic {
  background-image: url("./../res/Profile.png");
  width: 689px;
  height: 689px;
  transform: scale(0.7) translateY(10rem);
  position: absolute;
  top: 20%;
  right: 10%;
}

.services {
  padding: 100px 200px;
}
.services h1 {
  font-family: "Syne", sans-serif;
  font-weight: 900;
  font-size: 80px;
  text-transform: uppercase;
  width: 800px;
}
.services .line {
  height: 1px;
  width: 40vw;
  background-color: black;
  position: relative;
  left: -2%;
}
.services .services-div {
  padding: 20px 0;
}
.services .services-div h4 {
  font-size: 2rem;
  margin: 30px 0px;
}
.services .services-div p {
  width: 450px;
  margin-bottom: 20px;
  font-size: 20px;
}
.services .services-div--right {
  position: relative;
  left: 30%;
}

.story {
  background-color: #f8cc19;
  padding: 5rem 20rem;
}
.story-section {
  margin-bottom: 50px;
}
.story h1 {
  width: 900px;
  font-size: 8rem;
  font-family: "Syne", sans-serif;
  font-weight: 900;
  text-transform: uppercase;
}
.story h4 {
  margin: 20px 0;
  font-size: 20px;
}
.story p {
  font-size: 20px;
  font-weight: 400;
  line-height: 25px;
  letter-spacing: 1px;
  max-width: 95%;
  margin: 0 auto 20px auto;
}

.case-hero-pic {
  padding-top: 5rem;
  position: relative;
}
.case-hero-pic-2 {
  display: inline-block;
  position: absolute;
  top: 20%;
  left: 50%;
}
.case-hero-pic .dh-hero-2 {
  position: absolute;
  top: -10%;
  left: 75%;
}

.case-padding {
  height: 400px;
}

.dh-case-padding {
  height: 200px;
}

.pn-case-padding {
  height: 150px;
}

.header-bg {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  height: 90vh;
  width: 100%;
}

.header-bg-about {
  background-color: black;
}

.header-bg-kk {
  background-color: #43bccd;
}

.header-bg-dh {
  background-color: #da0037;
}

.header-bg-cs {
  background-color: #6c63ef;
}

.header-bg-pn {
  background-color: #08baf5;
}

.desc-sec {
  flex-direction: column;
  background-color: white;
  margin: 0 auto;
  padding-top: 0;
}
.desc-sec h2 {
  font-family: "Syne", sans-serif;
  font-size: 4rem;
  font-weight: 900;
  margin: 20px 0;
  text-transform: uppercase;
  width: 80%;
}
.desc-sec ul li {
  font-size: 2rem;
  width: 80%;
  margin: 10px auto;
  letter-spacing: 1px;
  line-height: 25px;
}
.desc-sec p,
.desc-sec p > a {
  font-size: 2rem;
}
.desc-sec h4 {
  max-width: 95%;
  margin: 0 auto 20px auto;
}
.desc-sec video {
  display: flex;
  margin: 30px auto;
}

.pn-services-video {
  width: 700px;
}

.case-image {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  margin: 30px 0;
  max-width: 80%;
}

.scroll-to-top {
  text-align: center;
  font-size: 38px;
  transform: rotate(-90deg);
  background-color: orange;
  width: 50px;
  height: 50px;
  position: fixed;
  z-index: 1000000;
  bottom: 20;
  right: 20;
  border-radius: 50%;
  display: none;
}
.scroll-to-top:hover {
  cursor: pointer;
}